.sidebar {
    flex: 0.35;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: rgb(238, 238, 238);
    border-right: 1px solid lightgray;
}

.sidebar__header {
    display: flex;
    align-items: center;
    padding: 10px;
    height: 50px;
}

.sidebar__avatar{
    cursor: pointer;
    margin: 10px;
}
.sidebar__input {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    background-color: rgb(200, 205, 206);
    color: grey;
    border-radius: 5px;
}
.sidebar__input > input {
    border: none;
    background: transparent;
    outline-width: 0;
}
.sidebar__chats {
    overflow: scroll;
}

/* disable scrollbar icon */
/* chrome, safari */
.sidebar__chats::-webkit-scrollbar {
    display: none;
}
/* firefox, edge */
.sidebar__chats {
    -ms-overflow-style: none;
    scrollbar-width: none;
}
